<template>
  <div>
    <loading :active.sync="isLoading"></loading>


    <div class="text-right mt-4">
        <button class="btn btn-primary" @click="openModal(true)"></button>
    </div>

    <table class="table mt-4">
        <thead>
            <tr>
            <th width="120"></th>
            <th></th>
            <th width="120"></th>
            <th width="120"></th>
            <th width="100"></th>
            <th width="80"></th>
            </tr>
        </thead>

        <tbody>
            <tr v-for="(item) in products" :key="item.id">
            <td>{{ item.category }}</td>
            <td>{{ item.title }}</td>
            <td class="text-right">
                {{ item.origin_price | currency }}
            </td>
            <td class="text-right">
                {{ item.price | currency }}
            </td>
            <td>
                <span v-if="item.is_enabled" class="text-success"></span>
                <span v-else></span>
            </td>
            <td class="d-flex">
                <button class="btn btn-outline-primary btn-sm" @click="openModal(false, item)"></button>
                <button class="btn btn-outline-danger btn-sm" @click="delModal(item)"></button>
            </td>
            </tr>
        </tbody>
    </table>
<!--
    <nav aria-label="Page navigation example">
      <ul class="pagination">
        <li class="page-item" :class="{'disabled': !pagination.has_pre }">
          <a class="page-link" href="#" aria-label="Previous"
            @click.prevent="getProducts(pagination.current_page - 1)">
            <span aria-hidden="true">&laquo;</span>
            <span class="sr-only">Previous</span>
          </a>
        </li>
        <li class="page-item" v-for="page in pagination.total_pages" :key="page"
          :class="{'active': pagination.current_page === page}">
          <a class="page-link" href="#" @click.prevent="getProducts(page)">{{ page }}</a>
        </li>
        <li class="page-item" :class="{'disabled': !pagination.has_next }">
          <a class="page-link" href="#" aria-label="Next"
            @click.prevent="getProducts(pagination.current_page + 1)">
            <span aria-hidden="true">&raquo;</span>
            <span class="sr-only">Next</span>
          </a>
        </li>
      </ul>
    </nav>
-->
    <Pagination :pages="pagination" @emitPages="getProducts"></Pagination>
    
    <!-- Modal:edit -->
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>

            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>

            <div class="modal-body">
                <div class="row">
                <div class="col-sm-4">
                    <div class="form-group">
                    <label for="image"></label>
                    <input type="text" class="form-control" id="image"
                        placeholder="" v-model="tempProduct.imageUrl">
                    </div>
                    <div class="form-group">
                    <label for="customFile"> 
                        <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                    </label>
                    <input type="file" id="customFile" class="form-control"
                        ref="files" @change="uploadFile">
                    </div>
                    <img img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                    class="img-fluid" :src="tempProduct.imageUrl" alt="">
                </div>
                <div class="col-sm-8">
                    <div class="form-group">
                    <label for="title"></label>
                    <input type="text" class="form-control" id="title"
                        placeholder=""  v-model="tempProduct.title">
                    </div>

                    <div class="form-row">
                    <div class="form-group col-md-6">
                        <label for="category"></label>
                        <input type="text" class="form-control" id="category"
                        placeholder="" v-model="tempProduct.category">
                    </div>
                    <div class="form-group col-md-6">
                        <label for="price"></label>
                        <input type="unit" class="form-control" id="unit"
                        placeholder="" v-model="tempProduct.unit">
                    </div>
                    </div>

                    <div class="form-row">
                    <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                        <input type="number" class="form-control" id="origin_price"
                        placeholder="" v-model="tempProduct.origin_price">
                    </div>
                    <div class="form-group col-md-6">
                        <label for="price"></label>
                        <input type="number" class="form-control" id="price"
                        placeholder="" v-model="tempProduct.price">
                    </div>
                    </div>
                    <hr>

                    <div class="form-group">
                    <label for="description"></label>
                    <textarea type="text" class="form-control" id="description"
                        placeholder="" v-model="tempProduct.description"></textarea>
                    </div>
                    <div class="form-group">
                    <label for="content"></label>
                    <textarea type="text" class="form-control" id="content"
                        placeholder="" v-model="tempProduct.content"></textarea>
                    </div>
                    <div class="form-group">
                    <div class="form-check">
                        <input class="form-check-input" type="checkbox"
                        id="is_enabled" :true-value="1" :false-value="0" 
                        v-model="tempProduct.is_enabled">
                        <label class="form-check-label" for="is_enabled">
                        
                        </label>
                    </div>
                    </div>
                </div>
                </div>
            </div>

          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="updateProduct"></button>
          </div>
        </div>
      </div>
    </div>
    
   <!-- Modal:delet -->
    <div
      class="modal fade"
      id="delProductModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{ tempProduct.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="delProduct"></button>
          </div>
        </div>
      </div>
    </div>


  </div>
</template>

<script>
import $ from 'jquery'; //vue $
import Pagination from '../Pagination';

export default {
 data (){
        return{
            products:[],
            pagination: {},
            tempProduct: {},
            isNew: false,
            isLoading: false,
            status: {
              fileUploading: false,
            },
        };
  },
  components: {
    Pagination,
  },
  methods: {
    getProducts(page = 1) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/products?page=${page}`; 
      const vm = this;
      console.log(process.env.APIPATH, process.env.CUSTOMPATH);
      vm.isLoading = true;  
      this.$http.get(api).then((response) => {
        console.log(response.data);
          vm.isLoading = false;
          vm.products = response.data.products;
          vm.pagination = response.data.pagination;
      });
    },
    openModal(isNew, item) {
      if (isNew) {
        this.tempProduct = {};
        this.isNew = true;
      } else {
        this.tempProduct = Object.assign({}, item);
        this.isNew = false;
      }
      $('#productModal').modal('show'); 
    },
    updateProduct() {
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`; 
      let httpMethod = 'post';
      const vm = this;
      if (!vm.isNew) {
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
        httpMethod = 'put';
      }
      console.log(process.env.APIPATH, process.env.CUSTOMPATH);
      this.$http[httpMethod](api, { data: vm.tempProduct }).then((response) => {    
        console.log(response.data);
        if (response.data.success) {
          $('#productModal').modal('hide');
          vm.getProducts();
        } else {
          $('#productModal').modal('hide');
          vm.getProducts();
          console.log('');
        }
        // vm.products = response.data.products;
      });
    },
    delModal(item){
      const vm = this;
      $('#delProductModal').modal('show');
      vm.tempProduct = Object.assign({}, item);
    },
    delProduct(){
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
      this.$http.delete(url).then((response)=>{
        console.log(response, vm.tempProduct);
        $('#delProductModal').modal('hide');
        vm.isLoading = false;
        this.getProducts(); //
      });

      // let vm = this;
      // const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
      // this.$http.delete(api).then((response) => {
      //   console.log(response.data);
      //   if(response.data.success){
      //     $("#delProductModal").modal("hide");
      //     vm.getProducts(); //
      //     console.log('');
      //   }else{
      //     $("#delProductModal").modal("hide");
      //     vm.getProducts();
      //     console.log('');
      //   }
      // });
    },
    uploadFile() {
      console.log(this); //Vuecomponents
      const uploadedFile = this.$refs.files.files[0];
      const vm = this;
      const formData = new FormData();
      formData.append('file-to-upload', uploadedFile);
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`;
      vm.status.fileUploading = true;
      this.$http.post(url, formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      }).then((response) => {
        console.log(response.data);
        vm.status.fileUploading = false;
        if (response.data.success) {
          // vm.tempProduct.imageUrl = response.data.imageUrl;
          // console.log(vm.tempProduct);
          vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl);  // url
        }else{
          this.$bus.$emit('message:push', response.data.message, 'danger');  //
        }
      });
    },
  },
  created() {  // methods 
    this.getProducts(); // getProducts 
  },
};

// updateProduct :
// tempProduct 
// 1. get  post;  vm.tempProduct {}
// 2.: modal
// 3.: isNew: false;  openModal; :  item 
// 4.this.tempProduct = item assig item 
// 5. isNew  (false) id put 
// 6. 

// uploadFile 
// 1.@change="uploadFile"  uploadFile
// 2.$refs.files.files 
// 3.formData 
// 4. url form-data
// 5.  $set

// loading:
// 1. isLoading  false  true 
// 2.vm.isLoading = true/ false;  getProducts 
// 3. fileUpload  icon 
// 4.vm.status.fileUploading = true/false; uploadFile 

//pagination:
// 1. data  pagination 
// 2. getProducts   vm.pagination = response.data.pagination;  
// 3. pagination 
// 4. v-for & :key & :class active  
// 5. disabled  has_nexthas_pre
// 6. page = 1 & products?page=${page} 
      // @click.prevent="getProducts(page)" 
// 7.pagination.current_page - 1 or + 1

// filter 
// 1.  currency.js
// 2.  main.js 
// 3.  |filter 
</script>




